import React, { Component } from 'react';
import banner from '../../../../sass/additions/img/banner.png'

class SummonerInfo extends Component {
  state={
    iconUrl: "",
    nickname: "",
    level: "",
  }

  componentDidMount(){
    setInterval(() => {
      this.setState({
        iconUrl: "xd",
        nickname: "G2 kylo",
        level: "121",
      })
    }, 1000);
  }

  render() {
    const {iconUrl,nickname,level} = this.state
    return (
      <div className="summonerInfo">
        <div className="banner">
          <img src={banner} alt="banner"/>
          <section>
            {iconUrl ? <img src="http://ddragon.leagueoflegends.com/cdn/9.17.1/img/profileicon/4240.png" alt="icon" className="icon"/> : null}
            <div className="nickname">{nickname ? <h1>{nickname}</h1>: <span className="loader">&nbsp;</span>}</div>
            <div className="level">{level ? <h2>{`Level ${level}`}</h2> : <span className="loader">&nbsp;</span> }</div>
          </section>
        </div>
      </div>
     );
  }
}
 
export default SummonerInfo;